import MasterPage from '../components/Master';
import fetch from 'isomorphic-unfetch';
import Price from '../components/Price';
import Notices from '../components/Notices';


const Index = (props) => (
    <MasterPage>
        <div className="row">
            <div className="col-12">
                <h2>Precio del Bitcoin</h2>
                <Price
                 // props={props.resPrice}
                />
            </div>
            <div className="col-md-8">
                <h2>Noticias Sobre Bitcoin</h2>
                <Notices
                    notices={props.notices}
                /> 
            </div>
            <div className="col-md-4">
                <h2>Proximos Eventos Bitcoin</h2>
                <Events

                />
            </div>
        </div>
    </MasterPage>
);

Index.getInitialProps = async () => {
//  const price = await fetch('')
    const notices = await fetch('https://newsapi.org/v2/everything?q=bitcoin&from=2019-04-10&sortBy=publishedAt&apiKey=549d84190a8c4bd49fb39bf8b9764349&language=es')
    const events = await fetch('https://www.eventbriteapi.com/v3/events/search/?q=Bitcoin&sort_by=date&location.address=Argentina&token=W7FVREUT46SCOS6KQFOM');
//  const resPrice = precio.json();
    const resNotices = await notices.json();
    const resEvents = await events.json();
    return{
//      priceBircoin : resPrice
        notices: resNotices.articles,
        events: resEvents.events
    }                        
}

export default Index;